/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import { Box, Flex, Heading } from 'rebass'
import styles from './styles'
import images from 'src/images'

function TokenMetrics (props) {
  return (
    <>
      <Box sx={styles.tokenMetricsChain}>{props.renderChain()}</Box>

      <Box sx={styles.tokenMetricsInfoChain} ml="3">
        <Box sx={styles.tokenMetricsSubHead}>Token Metrics</Box>

        <Box sx={styles.tokenMetricsInfoContainer}>
          <p sx={styles.tokenMetricsInfo}>
            NAME: <span sx={styles.info}>Hakka Finance (HAKKA)</span>
          </p>
          <p sx={styles.tokenMetricsInfo}>
            TYPE: <span sx={styles.info}>ERC-20 (Ethereum-based token)</span>
          </p>
          <p sx={styles.tokenMetricsInfo}>
            CONTRACT ADDRESS: <br />{' '}
            <Flex sx={styles.tokenMetricsInfoAddress} alignItems="center">
              <span sx={styles.info}>
                0x0E29e5AbbB5FD88e28b2d355774e73BD47dE3bcd
              </span>{' '}
              <img sx={styles.iconScan} src={images.iconEtherscan} alt="" />
            </Flex>
          </p>
        </Box>

        <Box sx={styles.tokenMetricsChainList}>
          Get HAKKA Token on Ethereum:
        </Box>
        <Flex sx={{ maxWidth: '70%' }} mt="2">{props.renderChainToken()}</Flex>
      </Box>
    </>
  )
}

export default TokenMetrics
