export default {
  coinContainer: {
    padding: '8px 16px',
    borderRadius: '8px',
    backgroundColor: '#f7fbfc',
    marginRight: '12px',
    cursor: 'pointer',
    margin: '12px 0 0 0',

    '@media screen and (max-width: 425px)': {
      width: '100%',
      margin: '12px 0 0 0',
      justifyContent: 'center'

    },

    ':hover': {
      border: '1px solid #dae1e3'
    }
  },

  coinHakka: {
    padding: '8px 16px',
    borderRadius: '8px',
    backgroundColor: '#f7fbfc',
    marginRight: '12px',
    cursor: 'pointer',
    marginTop: '12px',

    ':hover': {
      border: '1px solid #dae1e3'
    }
  },

  coinImg: {
    width: '24px',
    marginRight: '5px'
  },

  coinName: {
    fontSize: [0, 1, 2, 3],
    color: '#253e47'
  }
}
