/** @jsx jsx */
import { jsx } from 'theme-ui'
import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { Box, Button, Flex, Link, Text } from 'rebass'
import styles from './styles'
import images from 'src/images'
import SideBarItem from './SideBarItem'
import MyButton from 'src/components/Common/MyButton'
import { navigate } from 'gatsby'

const topSideBarItems = [
  {
    name: 'home',
    icon: images.iconHome,
    path: ''
  },
  {
    name: 'products',
    icon: images.iconProduct,
    path: 'products'

  }
]

const botSideBarItems = [
  {
    name: 'governance',
    icon: images.iconGovernance,
    path: 'governance'

  },
  {
    name: 'rewards',
    icon: images.iconReward,
    path: 'rewards'

  },
  {
    name: 'vesting',
    icon: images.iconVesting,
    path: 'vesting'

  },
  {
    name: 'vault',
    icon: images.iconVault,
    path: 'vault'

  }
]

function SideBar (props) {
  const [selectedNav, setSelectedNav] = useState('')

  const onSelectNavItem = (path) => () => {
    setSelectedNav(path)
    navigate(`/${path}`)

    // console.log(path);
  }

  const renderTopSideBar = () => {
    return topSideBarItems.map((it, idx) => {
      return (
        <Box key={it.name} onClick={onSelectNavItem(it.path)} mt="3" >
              <SideBarItem
                selectedNav={selectedNav}
                icon={it.icon}
                text={it.name}
                path={it.path}
              />
            </Box>
      )
    })
  }

  const renderBotSideBar = () => {
    return botSideBarItems.map((it, idx) => {
      return (
        <Box key={it.name} mt="3">
          <SideBarItem
            selectedNav={selectedNav}
            icon={it.icon}
            text={it.name}
            path={it.path}
          />
        </Box>
      )
    })
  }
  return (
    <Box sx={styles.sidebar}>
      <Box sx={styles.custom_padding}>
        <img src={images.hakkaLogo} />
        {renderTopSideBar()}
      </Box>

      <Box sx={styles.hl} ml="25px" mt="0" pr="0"></Box>

      <Box sx={styles.custom_padding}>
        <Box pl="12px">DAO</Box>
        {renderBotSideBar()}
      </Box>

      <Box sx={styles.hl} ml="25px" mt="0" pr="0"></Box>

      <Box sx={styles.custom_padding}>
        <Box pl="12px">News</Box>

        <Box sx={styles.medium_content} mt="3">
          <Flex alignItems="center">
            <img sx={styles.left_icon} src={images.iconMedium} alt />
            <Box ml="2">Medium </Box>
          </Flex>
          <Box mt="2" mr="2">
            <img src={images.iconLinkSmall} />
          </Box>
        </Box>
      </Box>

      <Box sx={styles.hl} mt="2" pr="0"></Box>

      <Box p="2" mt="3">
        <MyButton>Learn More</MyButton>
      </Box>
      <Box sx={styles.news_text} p="3" textAlign="center">
        or{' '}
        <Link sx={styles.news_text} href="#">
          contact us
        </Link>
      </Box>
    </Box>
  )
};
export default React.memo(SideBar)
