export default {
  homescreenContainer: {
    // position: "relative",
    // top: "-260px",
    padding: '0 4% 0 12.8%',

    '@media screen and (max-width: 425px)': {
      padding: '0 16px'
    }
  },

  homescreenSubTitle: {
    color: '#2da287',
    fontSize: [0, 1, 2, 3]
  },

  spanLink: {
    textDecoration: 'underline',
    marginLeft: '3px',
    fontSize: [0, 1, 2, 3]
  },

  homeHeading: {
    fontSize: [5, 6, 7, 7],
    color: '#0b555a',
    width: '80%',
    fontWeight: '300',
    margin: '36px 0 80px'
  },

  // Card top
  homeCardTop: {
    // marginBottom: '80px'
  },

  homeTotalValue: {
    padding: '0 12.8%',
    paddingRight: 0,
    marginBottom: '176px',

    '@media screen and (max-width: 425px)': {
      padding: '0 16px',
      paddingRight: 0
    }
  },

  // what hakka
  whatHakka: {
    marginBottom: '88px'
  },

  // token metrics
  tokenMetrics: {
    marginBottom: '160px',
    width: '100%',

    '@media screen and (max-width: 425px)': {
      flexDirection: 'column'
    }
  },

  chainContent: {
    maxWidth: '350px',
    '@media screen and (max-width: 425px)': {
      flexDirection: 'column',
      alignItems: 'center'
    }
  },

  imgChain: {
    width: '40px',
    height: '40px',
    padding: '8px',
    backgroundColor: '#f2f2f2',
    borderRadius: '8px',

    '@media screen and (max-width: 425px)': {
      marginBottom: '12px'
    }
  },

  chainName: {
    fontSize: [2, 3, 4, 5],
    color: '#253e47',
    fontWeight: 500
  },

  iconRight: {
    '@media screen and (max-width: 425px)': {
      display: 'none'
    }
  },

  // utility hakka
  utilityHakka: {
    marginBottom: '160px'
  },

  // partners and intergrator
  partners: {
    marginBottom: '160px'
  },

  partnersHead: {
    fontSize: [5, 6, 7, 7],
    color: '#253e47',
    fontWeight: 'normal',
    marginBottom: '36px'
  },

  imgPartner: {
    width: '20%',
    marginRight: '28px',

    '@media screen and (max-width: 425px)': {
      width: '45%'
    },

    ':nth-child(2)': {
      '@media screen and (max-width: 425px)': {
        marginRight: 0
      }
    },

    ':last-child': {
      '@media screen and (max-width: 425px)': {
        width: '80%',
        margin: '12px auto 0'
      }
    }
  }
}
