export default {
  // token metrics
  tokenMetricsChain: {
    width: '30%',
    padding: '16px 12px',
    paddingLeft: 0,
    fontSize: [2, 3, 4, 5],
    flex: '0.8',

    '@media screen and (max-width: 425px)': {
      width: '100%',
      display: 'flex',
      justifyContent: 'space-between'
    }
  },

  tokenMetricsInfoChain: {
    // width: "30%",
    marginLeft: '105px',
    borderTop: '1px solid #2da287',
    paddingTop: '27px',
    flex: '1',

    '@media screen and (max-width: 425px)': {
      marginLeft: 0,
      borderTop: 'unset'
    }
  },

  tokenMetricsInfoContainer: {
    marginBottom: '20px'
  },

  tokenMetricsSubHead: {
    fontSize: [1, 2, 3, 4],
    color: '#253e47',
    fontWeight: 'bold'
    // marginBottom: '20px'
  },

  iconScan: {
    width: '24px'
  },

  tokenMetricsInfo: {
    fontSize: [0, 1, 2, 3],
    fontWeight: 600,
    color: '#253e47'
  },

  tokenMetricsInfoAddress: {
    cursor: 'pointer',

    '& iconScan': {
      opacity: '.5'
    },

    ':hover': {
      '& iconScan': {
        opacity: 1
      }
    }
  },

  info: {
    fontWeight: 'normal',
    textDecoration: 'underline'
  },

  tokenMetricsChainList: {
    fontSize: [0, 1, 2, 3],
    fontWeight: 600,
    color: '#253e47'
    // marginBottom: '20px'
  }
}
