/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import PropTypes from 'prop-types'
import { Box, Flex, Text } from 'rebass'
import styles from './styles'
import { upperCaseFirstLetter } from 'src/common/functions'

const SideBarItem = (props) => {
  const { icon, text, path, selectedNav } = props
  console.log('selectedNav', selectedNav)

  // const currentPath = typeof window !== 'underfined' ? window.location.href.split('/').reverse()[0] : undefined
  // console.log(currentPath);

  return (
        <Box sx={selectedNav === path ? styles.sidebar_item_active : styles.sidebar_item}>

            <div>
                <img src={icon} alt/>
            </div>

            <Text sx={styles.sidebar_text} className="sidebar-text" ml='2'>
                {upperCaseFirstLetter(text)}
            </Text>
        </Box>
  )
}
export default SideBarItem
