/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import { Box, Flex, Heading } from 'rebass'
import Layout from 'src/containers/Layout'
import images from 'src/images'
import styles from './styles'
import CardInfo from './component/CardInfo'
import TotalValueLock from './component/TotalValueLock'
import CoinComponent from '../Common/CoinComponent'
import WhatHakka from './component/WhatHakka'
import TokenMetrics from './component/TokenMetrics'
import UtilityHakka from './component/UtilityHakka'
import HakkaTeam from './component/HakkaTeam'

const HomeScreen = (props) => {
  const coins = [
    {
      imageCoin: 'iconCoingecko',
      coinName: 'Coingecko'
    },
    {
      imageCoin: 'iconCoinmarketcap',
      coinName: 'CoinMarketCap'
    }
  ]

  const chains = [
    {
      imgChain: 'iconEthereum',
      chainName: 'Ethereum Mainnet'
    },
    {
      imgChain: 'iconBinanceGray',
      chainName: 'Binance Smart Chain'
    }
  ]

  const chainTokens = [
    {
      imageCoin: 'iconInch',
      coinName: '1inch'
    },
    {
      imageCoin: 'iconUniswap',
      coinName: 'Uniswap'
    },
    {
      imageCoin: 'iconBalancer',
      coinName: 'Balancer'
    }
  ]

  const partnersImg = [
    {
      img: 'image1inch'
    },
    {
      img: 'imageChainlink'
    },
    {
      img: 'imageSynthetix'
    }
  ]

  const renderCoin = () => {
    return coins.map((item, i) => {
      return <CoinComponent key={item.coinName} item={item} i={i} />
    })
  }

  const renderChain = () => {
    return chains.map((chain, i) => {
      return (
        <Flex
          sx={styles.chainContent}
          alignItems="center"
          justifyContent="space-between"
          key={i}
          mb="4"
        >
          <Flex alignItems="center">
            <img sx={styles.imgChain} src={images[chain.imgChain]} alt="" />
            <Box sx={styles.chainName} ml="3">
              {chain.chainName}
            </Box>
          </Flex>
          <Box ml="3">
            <img sx={styles.iconRight} src={images.iconRight} alt="" />{' '}
          </Box>
        </Flex>
      )
    })
  }

  const renderChainToken = () => {
    return chainTokens.map((item, i) => {
      return <CoinComponent key={item.coinName} whatHakka={true} item={item} i={i} />
    })
  }

  const renderPartnersImg = () => {
    return partnersImg.map((item) => {
      return <img key={item.img} sx={styles.imgPartner} src={images[item.img]} alt="" />
    })
  }

  return (
    <>
      <Box>

        <Box ml="12.8%" mr="4%">
          <Flex alignItems="center" sx={styles.homescreenSubTitle}>
            <img src={images.iconFlower} alt="" />
            Hakka Finance is <span sx={styles.spanLink}>hiring</span>. Come join
            us!
          </Flex>

          <Box sx={styles.homeHeading} mt="4" mb="5">
            The Most Diversified Decentralized Finance Ecosystem
          </Box>

          <Box
            // justifyContent="flex-start"
            // flexWrap="wrap"
            sx={styles.homeCardTop}
            mt="4"
          >
            <CardInfo />
          </Box>
        </Box>

        <Box sx={styles.homeTotalValue} mt="6">
          <TotalValueLock />
        </Box>

        <Box ml="12.8%" mr="4%">
          <Box sx={styles.whatHakka} mt="6">
            <WhatHakka renderCoin={renderCoin} />
          </Box>

          <Flex sx={styles.tokenMetrics} mt="5" >
            <TokenMetrics
              renderChainToken={renderChainToken}
              renderChain={renderChain}
            />
          </Flex>

          <Box sx={styles.utilityHakka} mt="6">
            <UtilityHakka />
          </Box>

          <Box sx={styles.partners} mt="6">
            <Box sx={styles.partnersHead}>Partners &amp; Integrators</Box>

            <Flex flexWrap="wrap">{renderPartnersImg()}</Flex>
          </Box>

          <Box mt="6"><HakkaTeam /></Box>
        </Box>
      </Box>
    </>
  )
}
export default HomeScreen
