/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import { Box, Flex, Heading } from 'rebass'
import styles from './styles'
import images from 'src/images'

function HakkaTeam () {
  const listMenbers = [
    {
      name: 'Jack Lai',
      job: 'research'
    },
    {
      name: 'Wego Chen',
      job: 'ADVISOR'
    },
    {
      name: 'Ian Hsu',
      job: 'ADVISOR'
    }
  ]

  const renderListMembers = () => {
    return listMenbers.map((item, i) => {
      return (
        <Flex
          sx={styles.hakkaTeamInfoUser}
          mr={6}
          flexDirection="column"
          key={i}
        >
          <img sx={styles.hakkaTeamImgUserList} src={images.iconUser} alt="" />
          <Box sx={styles.hakkaTeamName} mt="3">{item.name}</Box>
          <Box sx={styles.hakkaTeamJob} mt="2">{item.job}</Box>
        </Flex>
      )
    })
  }

  return (
    <Box sx={styles.hakkaTeam}>
      <Box sx={styles.hakkaTeamHead}>Hakka Team</Box>
      <Flex sx={styles.hakkaTeamUser} mt="4">
        <img sx={styles.hakkaTeamImgUser} src={images.iconUser} alt="" />

        <Box sx={styles.hakkaTeamInfo} ml="4" >
          <Box sx={styles.hakkaTeamName}>Ping Chen</Box>
          <Box sx={styles.hakkaTeamJob}>FOUNDER</Box>
          <p sx={styles.hakkaTeamComment}>
            Ping is the founder of the HAKKA ecosystem and has been involved in
            the Ethereum community for over 5 years as a dApp developer.
          </p>
        </Box>
      </Flex>
      <Flex sx={styles.hakkaTeamListMember} mt="4" flexWrap="wrap">
        {renderListMembers()}
      </Flex>
      <Box mt="4"><hr sx={styles.dividerLine} /></Box>
      <Flex justifyContent="space-between" alignItems="center" mb="6">
        <Box sx={styles.hakkaTeamFootText}>Know more about Hakka Team</Box>
        <Flex sx={styles.hakkaTeamFootTextLink} alignItems="center">
          Visit Wiki
          <img src={images.iconLinkSmallGreen} alt="" />
        </Flex>
      </Flex>
    </Box>
  )
}

export default HakkaTeam
